
import Loading from 'vux/src/components/loading/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Scroll from './pulldown'

export default {
components: {
Loading,
Spinner,
LoadMore,
Swiper,
SwiperItem,
Scroller,
Cell,
Group,
Badge,
Divider,
Flexbox,
FlexboxItem,
'v-scroll': Scroll
},
data () {
return {
cellTitle: '首投 xxx金融',
imgList: [],
imgType: 2,
loading: false,
spinner: false,
loadmore: false,
loadbottom: false,
activityList: []
}
},
created: function () {
var params = 'type=' + this.imgType
this.$http.post(this.domain + '/api/anon/index/getSwiper', params).then((response) => {
this.imgList = response.data.data
}).catch((response) => {
this.$vux.toast.text('系统异常!', 'middle')
})
this.fetchData()
},
watch: {

'$route': 'fetchData'
},
methods: {
fetchData: function () {
this.loadmore = true
this.$http.post(this.domain + '/api/anon/activity/getHotActivity', '').then((response) => {
this.activityList = response.data.data
this.loadmore = false
}).catch((response) => {
this.loadmore = false
this.$vux.toast.text('系统异常!', 'middle')
})
},
onRefresh (done) {

this.loadbottom = false
this.activityList = []
this.fetchData()
done()
},
onInfinite (done) {

}
}
}



